.button,
.button1,
.button2,
.f1 {
  position: relative;
  font-weight: 900;
}
.gropt-of-buttons,
.navigation {
  display: flex;
  flex-direction: row;
}
.gropt-of-buttons {
  align-items: flex-start;
  justify-content: flex-start;
  gap: 36px;
  font-size: var(--font-size-22xl-5);
}
.navigation {
  position: absolute;
  width: calc(100% - 39px);
  top: -12px;
  right: 17px;
  left: 22px;
  align-items: center;
  justify-content: space-between;
}
.heading {
  align-self: stretch;
  position: relative;
  display: inline-block;
  height: 261px;
  flex-shrink: 0;
}
.design,
.hey-this-is {
  margin: 0;
}
.hey-this-is-container {
  align-self: stretch;
  flex: 1;
  position: relative;
  font-size: 64.4px;
  font-weight: 900;
}
.container-child {
  align-self: stretch;
  position: relative;
  background-color: #e98787;
  height: 76px;
}
.container {
  flex: 1;
  height: 684px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 54px;
}
.content-child {
  align-self: stretch;
  width: 538px;
  position: relative;
  background-color: var(--color-gainsboro);
}
.content {
  position: absolute;
  width: calc(100% - 337px);
  top: 203px;
  right: 124px;
  left: 213px;
  height: 684px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  font-size: 141.9px;
}
.macbook-pro-16-1 {
  width: 100%;
  position: relative;
  background-color: var(--color-white);
  height: 1117px;
  overflow: hidden;
  text-align: left;
  font-size: 151.9px;
  color: var(--color-black);
  font-family: var(--font-inter);
}
